.content_holder{
    display: flex;
    flex-direction: row;
    width: 80vw;
    height: 30vh;
    margin: 15px auto;
    border: 1px solid black;
    border-radius: 20px;
    border: 2px solid #154c79;
}
.img {
    width: 30vw;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    border-radius: 20px 0 0 20px;
}
.content {
    width: 50vw;
    height: 100%;
    padding: 30px;
}
.content >* {
    margin-bottom: 10px;
}
.btn {
    background-color: #154c79;
    padding: 10px 20px;
    border-radius: 10px;
    border: none;
}
.link {
    text-decoration: none;
    color: rgb(241, 241, 12);
    font-size: 15px;
}