.details_holder {
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    width: 70vw;
    margin: 0 auto;
    padding-top: 100px;
}
.product_img {
    width: 200px;
    height: 200px;
    background-size: cover;
    background-position: center;
}
.details_content > * {
    margin-bottom: 10px;
}
.btn {
    padding: 10px 20px;
    background-color: #154c79;
    border: none;
}
.link {
    text-decoration: none;
    font-size: 15px;
    color: rgb(241, 241, 12);
}