.login {
    margin: 0 auto;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
}
.login > * {
    margin-bottom: 15px;
}
.input {
    font-size: 15px;
    padding: 10px 20px;
    margin-right: 10px;
    border-radius: 5px;
    width: 300px;
}
.btn {
    font-size: 20px;
    padding: 10px 20px;
}