.holder {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.search_holder {
    margin: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}
.new_product >* {
    font-size: 20px;
    margin: 0 10px;
}
.new_product input {
    padding: 10px;
    border: 2px solid #154c79;
}
.btn {
    font-size: 20px;
    padding: 10px 20px;
    background-color: #264e6e;
    color: rgb(235, 235, 64);
    border: none;
}